.Friends_item {
   margin-left: 20px;
   margin-bottom: 5px;
   width: 100%;
   text-align: left;
}
.Friends_item a{
  color: #fff;
}
.Friends_item a:hover{
   font-size: 1.05em;
   color: gold;
 }

.Friends_item .img{
   margin-right: 10px;
   display: inline-block;
   width: 20px;
   height: 20px;   
   border-radius: 50% 50%;
   background-color: rgb(243, 207, 130);
}
.Friends_item .active {
   color: gold;
}