.item{
    width: max-content;
    display: grid;
    grid-template-columns: 0.3fr 1fr;
    border: 1px solid;
    border-radius: 5px;
    margin: 15px;
    padding: 5px;
}

.item img{
    width: 100px;
}
article, .like_Block{
    display: grid;
    grid-template-columns: 1fr 3fr;
}
.like_Block img{
    width: 40px;
}
article{
    width: max-content;
    border-top: 1px solid;
    padding: 5px;
    margin: 5px;
}

article button{
    position: relative;
    left: 55%;
    width: max-content;
    background: red;
    padding: 5px;
}