
.h1{
   color: rgb(58, 223, 37);
}
.userCard{
   display: grid;
   grid-template-columns: 0.3fr 1fr;
   margin: 20px;
   border: 1px solid rgb(159, 159, 161);
   border-radius: 10px;
}
.leftBlock{
   width: 80%;
   margin: 0 auto;
   padding: 5px;
   display: grid;
   grid-template-rows: 1fr 0.1fr;
}
.leftBlock img{
   display: inline-block;
   width: 70px;
   height: 70px;
   border-radius: 50%;
   margin-bottom: 5px;
}
.leftBlock button{
   width: 80px;
   padding: 5px 3px;
   font-size: 12px;
}
.rightBlock{
   padding: 10px;
   display: grid;
   grid-template-columns: 1fr 1fr;
}
.element_name{
   margin-bottom: 10px;
}
.element_b{
   text-align: right;
}

.selectedPage{
   font-weight: bold;
}
.imgLoading{
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 750px;
   z-index: 10;
   background-color: rgba(250,250,250, 0.5);
   
}

.imgLoading img{
   display: block;
   background-color: rgba(20,20,20, 0.7);
   margin: 0 auto;
}